
import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { fetchAllOrdersAsync, selectOrders, selectTotalOrders, updateOrderAsync } from '../../../features/order/orderSlice';
import { ArrowUpIcon, ArrowDownIcon, EyeIcon, PencilIcon } from '@heroicons/react/24/outline';
import Pagination from '../../../features/common/Pagination';
import { ITEM_PER_PAGE } from '../../../app/constants';

const AdminOrders = () => {
    const [page, setPage] = useState(1);
    const dispatch = useDispatch();
    const orders = useSelector(selectOrders);
    const totalOrders = useSelector(selectTotalOrders);
    const [editableOrderId, setEditableOrderId] = useState(-1);
    const [sort, setSort] = useState({});
    const [sortOption, setSortOption] = useState({ sortBasis: 'totalAmount' });
    const [sortOptionId, setSortOptionId] = useState({ sortBasis: 'orderNumber' });

    useEffect(() => {
        const pagination = { _page: page, _per_page: ITEM_PER_PAGE };
        dispatch(fetchAllOrdersAsync({ sort, pagination }));
    }, [dispatch, page, sort]);

    const handleShow = (item) => {
        console.log("handle show");
    };

    const handleEdit = (order) => {
        setEditableOrderId(order.id);
    };

    const handleUpdate = (e, order) => {
        const updatedOrder = { ...order, status: e.target.value };
        dispatch(updateOrderAsync(updatedOrder));
        setEditableOrderId(-1);
    };

    const handleSort = (option) => {
        const updatedSort = { _sort: option.sortBasis };
        setSortOption({ sortBasis: option.sortBasis });
        setSort(updatedSort);
    };

    const handleSortId = (option) => {
        const updatedSort = { _sort: option.sortBasis };
        setSortOptionId({ sortBasis: option.sortBasis });
        setSort(updatedSort);
    };

    const chooseColor = (status) => {
        switch (status) {
            case 'pending':
                return 'bg-purple-200 text-purple-600';
            case 'dispatched':
                return 'bg-yellow-200 text-yellow-600';
            case 'delivered':
                return 'bg-green-200 text-green-600';
            case 'cancelled':
                return 'bg-red-200 text-red-600';
            default:
                return 'bg-purple-200 text-purple-600';
        }
    };

    const handlePage = (page) => {
        setPage(page);
    };

    // Example: Mapping orders and adding orderNumber property
    const displayOrders = orders.map((order, index) => ({
        ...order,
        orderNumber: index + 1,
    }));

    return (
        <div>
            <div className=" p-2">
                <div className="bg-gray-100 flex items-center justify-center font-sans overflow-hidden">
                    <div className="w-full">
                        <div className="bg-white shadow-md rounded my-6">
                            <table className="min-w-max w-full table-auto">
                                <thead>
                                    <tr className="bg-gray-200 text-gray-600 uppercase text-sm leading-normal">
                                        <th className="py-3 px-5 text-center cursor-pointer" onClick={() => handleSortId({ sortBasis: sortOptionId.sortBasis === 'orderNumber' ? '-orderNumber' : 'orderNumber' })}>
                                            Order# {(sort._sort === 'orderNumber' ? <ArrowUpIcon className='w-4 h-4 inline' /> : <ArrowDownIcon className='w-4 h-4 inline' />)}
                                        </th>
                                        <th className="py-3 px-5 text-left">Items</th>
                                        <th className="py-3 px-5 text-center cursor-pointer" onClick={() => handleSort({ sortBasis: sortOption.sortBasis === 'totalAmount' ? '-totalAmount' : 'totalAmount' })}>
                                            Total Amount {(sort._sort === 'totalAmount' ? <ArrowUpIcon className='w-4 h-4 inline' /> : <ArrowDownIcon className='w-4 h-4 inline' />)}
                                        </th>
                                        <th className="py-3 px-5 text-center">Shipping Address</th>
                                        <th className="py-3 px-5 text-center">Status</th>
                                        <th
                                            className="py-3 px-5 text-center cursor-pointer"
                                        >
                                            Order Time{' '}
                                        </th>
                                        <th
                                            className="py-3 px-5 text-center cursor-pointer"
                                        >
                                            Last Updated{' '}
                                        </th>
                                        <th className="py-3 px-5 text-center">Actions</th>
                                    </tr>
                                </thead>
                                <tbody className="text-gray-600 text-sm font-light">
                                    {displayOrders.map((order) => (
                                        <tr key={order.id} className="border-b border-gray-200 hover:bg-gray-100">
                                            <td className="py-3 px-0 text-left whitespace-nowrap">
                                                <div className="flex items-center">
                                                    <div className="mr-2">
                                                        {order.id}
                                                    </div>
                                                </div>
                                            </td>
                                            <td className="py-3 px-0 text-left">
                                                {order.items.map((item, index) => (
                                                    <div key={index} className="flex items-center">
                                                        <div className="mr-2">
                                                            <img
                                                                className="w-6 h-6 rounded-full"
                                                                src={item.product.thumbnail}
                                                                alt={item.product.title}
                                                            />
                                                        </div>
                                                        <span>{item.product.title} - #{item.quantity} - ${(item.product.discountPrice)}</span>
                                                    </div>
                                                ))}
                                            </td>
                                            <td className="py-3 px-0 text-center">
                                                <div className="flex items-center justify-center">
                                                    ${order.totalAmount}
                                                </div>
                                            </td>
                                            <td className="py-3 px-0 text-center">
                                                <div className="items-center justify-center">
                                                    <div><strong>{order.selectedAddress.name}</strong></div>
                                                    <div>{order.selectedAddress.street}</div>
                                                    <div>{order.selectedAddress.city}</div>
                                                    <div>{order.selectedAddress.state}</div>
                                                    <div>{order.selectedAddress.pinCode}</div>
                                                    <div>{order.selectedAddress.phone}</div>
                                                </div>
                                            </td>
                                            <td className="py-3 px-0 text-center">
                                                {order.id === editableOrderId ?
                                                    (<select onChange={(e) => handleUpdate(e, order)}>
                                                        <option value="pending">Pending</option>
                                                        <option value="dispatched">Dispatched</option>
                                                        <option value="delivered">Deliver</option>
                                                        <option value="cancelled">Cancelled</option>
                                                    </select>) :
                                                    (<span className={`${chooseColor(order.status)} py-1 px-3 rounded-full text-xs`}>
                                                        {order.status}
                                                    </span>)
                                                }
                                            </td>
                                            {/* <td className="py-3 px-0 text-center">
                                                <div className="flex items-center justify-center">
                                                    {order.createdAt ? new Date(order.createdAt).toLocaleString() : null}
                                                </div>
                                            </td>
                                            <td className="py-3 px-0 text-center">
                                                <div className="flex items-center justify-center">
                                                    <div className="flex items-center justify-center">
                                                        {order.updatedAt ? new Date(order.updatedAt).toLocaleString() : null}
                                                    </div>
                                                </div>
                                            </td> */}
                                            <td className="py-3 px-0 text-center break-words w-24">
                                                <div className="flex items-center justify-center">
                                                    {order.createdAt ? new Date(order.createdAt).toLocaleString() : null}
                                                </div>
                                            </td>
                                            <td className="py-3 px-0 text-center break-words w-24">
                                                <div className="flex items-center justify-center">
                                                    {order.updatedAt ? new Date(order.updatedAt).toLocaleString() : null}
                                                </div>
                                            </td>


                                            <td className="py-3 px-6 text-center">
                                                <div className="flex item-center justify-center">
                                                    <div className="w-4 mr-4 transform hover:text-purple-500 hover:scale-110 cursor-pointer">
                                                        <EyeIcon className='w-6 h-6' onClick={() => handleShow(order)} />
                                                    </div>
                                                    <div className="w-4 mr-4 transform hover:text-purple-500 hover:scale-110 cursor-pointer">
                                                        <PencilIcon className='w-6 h-6' onClick={() => handleEdit(order)} />
                                                    </div>
                                                </div>
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
            <Pagination
                page={page}
                setPage={setPage}
                handlePage={handlePage}
                totalItems={totalOrders}
            />
        </div>
    );
};

export default AdminOrders;